.popup {
  display: none;
}

.popup--active {
  display: block;
}

.popup__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.95;
  background-color: #e5e5e5;
}

.popup__wrapper {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 480px;
  height: 521px;
  padding: 56px 48px 40px 48px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

  &--success {
    padding: 219px 60px 112px 60px;

    &::after {
      content: "";
      position: absolute;
      top: 122px;
      left: 50%;
      transform: translateX(-50%);
      width: 71px;
      height: 64px;
      background-image: url("../../img/checkmark.png");
      background-repeat: no-repeat;
    }
  }
}

.popup__close {
  position: absolute;
  background-color: transparent;
  border: none;
  width: 18px;
  height: 17px;
  padding: 0;
  top: 32px;
  right: 43px;

  &::after,
  &::before {
    content: "";
    position: absolute;
    width: 21px;
    height: 2px;
    background-color: $color-yellow;
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(-45deg);
  }
}

.popup__title {
  text-transform: none;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: -0.02em;

  &--success {
    text-align: center;
  }
}

.popup__description {
  padding-right: 25px;
  margin-bottom: 32px;

  &--success {
    padding-right: 0;
    text-align: center;
  }
}

.popup__item {
  display: block;
  margin: 0;
  margin-right: 24px;
  margin-bottom: 9px;
}

.popup__item input {
  width: 100%;
  padding: 18px 38px 18px 38px;
  border-radius: 80px;
  background-color: transparent;
  outline: 0;
  border: 2px solid #e3e3e3;
  font: inherit;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  font-variant: small-caps;
  opacity: 0.87;
  cursor: pointer;

  &:hover {
    border-color: $color-yellow;
  }

  &:focus {
    border-color: $color-black;
    cursor: text;
  }
}

.popup__button {
  display: block;
  margin: 0 auto;
  margin-top: 22px;
  margin-bottom: 18px;
  min-width: 264px;
  padding-left: 63px;
  padding-right: 63px;
  text-align: center;
}

.popup__agreement {
  display: flex;
  width: 264px;
  margin: 0 auto;
}

.popup__agreement label {
  position: relative;
  padding-left: 25px;
  font-size: 13px;
  line-height: 19px;
  opacity: 0.74;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    padding: 1px;
    width: 16px;
    height: 16px;
    border: 2px solid;
    border-image: $gradient;
    border-image-slice: 1;
    border-radius: 3px;
  }
}

.popup__agreement input:checked + label {
  &::before {
    background-clip: content-box;
    background-image: $gradient;
  }
}
